import React, {useState} from 'react'

const {ceil,random} = Math;


export default props => {
    const [numeros, setNumeros] = useState(gerarNumeros());

    function gerarNumeroNaoContido(arr) {
        let newNumber = ceil(60*random());
        for(let i=0; i<arr.length; i++)
            if(arr[i] == newNumber)
                return gerarNumeroNaoContido(arr);
        return newNumber;
    }
    
    function gerarNumeros() {
        let arr = [];
        for(let i=0; i<props.qtdeNumero; i++)
            arr.push(gerarNumeroNaoContido(arr));
        return arr.sort((a, b) => a - b);
    }

    return (
        <div>
            <h3>Mega</h3>
            <h4>{numeros.join(' ')}</h4>
            <button onClick={()=>{setNumeros(gerarNumeros())}}>Gerar números</button>
        </div>
    )
}